<template>
 <div class="course-construction">
   <title-curmb></title-curmb>
   <course-steps :title="form.courseName" :step="1"></course-steps>
   <div class="course-construction_body">
       <el-form :model="form" :rules="rules" ref="form" label-width="80px">
            <h2>基本信息</h2>
            <el-row :gutter="24">
                <el-col :span="11">
                    <el-form-item label="课程代码" prop="courseCode">
                        <el-input v-model="form.courseCode"  disabled placeholder="" class="my-input"></el-input>
                    </el-form-item>
                    <el-form-item label="学时" prop="period">
                        <el-input-number v-model="form.period"  :min="0" disabled placeholder="" controls-position="right" class="my-input"></el-input-number>
                    </el-form-item>
                    <el-form-item label="试用专业" prop="specialName">
                        <el-input v-model="form.specialName"  disabled placeholder="" class="my-input"></el-input>               
                    </el-form-item>
                     <el-form-item label="课程类型" prop="className">
                          <el-input v-model="form.className"  disabled placeholder="" class="my-input"></el-input>                  
                    </el-form-item>
                </el-col>
                <el-col :span="11" :offset="1">
                    <el-form-item label="课程名称" prop="courseName">
                        <el-input v-model="form.courseName"  disabled placeholder="" class="my-input"></el-input>
                    </el-form-item>
                     <el-form-item label="英文名称" prop="enName">
                        <el-input v-model="form.enName" disabled placeholder="" class="my-input"></el-input>
                    </el-form-item>
                    <el-form-item label="学分" prop="credit">
                        <el-input-number v-model="form.credit"  :min="0" disabled placeholder="" controls-position="right" class="my-input"></el-input-number>
                    </el-form-item>
                    <el-form-item label="先修课程" prop="courseIds">
                       <el-select v-model="selectCourse"  @change="selectChange" filterable  multiple placeholder="请选择" class="my-input">
                            <el-option
                            v-for="item in courseOptions"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
             <h2>课程性质</h2>
             <el-row>
                <el-form-item label="课程描述" prop="remarks">
                    <el-input v-model="form.remarks"  type="textarea" :rows="9" placeholder="请对课程进行简单的描述..."></el-input>
                </el-form-item>   
            </el-row>
            <el-row :gutter="24">
                <el-col :span="6" :offset="9">
                    <div class="c-footer">
                        <el-button @click="formSubmit('form', 'save')">保&nbsp;&nbsp;存</el-button>
                        <el-button type="primary" @click="formSubmit('form', 'next')">下一步</el-button>
                    </div>
                </el-col>
            </el-row>
       </el-form>
   </div>
 </div>
</template>

<script>
import TitleCurmb from './components/TitleCurmb'
import CourseSteps from './components/CourseSteps'

export default {
 name: 'BasicInfo',
 components: {TitleCurmb, CourseSteps},
 data() {
    const checkCourseIds = (rule, value, callback) => {
        if(this.selectCourse.length !==0){
              callback()
        }else{
             callback(new Error('请选择先修课程'))
        }
    };
 return {
  courseOptions:[],
  selectCourse:[],
  form:{
      courseCode:'',
      period:'',
      specialName:'',
      courseName:'',
      credit:'',
      courseIds:'',
      enName:'',
      className:'',
      remarks:''
  },
  rules:{
    courseCode: [{ required: true, message: "请输入课程代码", trigger: "blur"}],  
    period: [{ required: true, message: "请设置学时", trigger: "blur"}], 
    specialName: [{ required: true, message: "请选择试用专业", trigger: "blur"}], 
    courseName: [{ required: true, message: "请输入课程名称", trigger: "blur"}], 
    credit: [{ required: true, message: "请设置学分", trigger: "blur"}], 
    courseIds: [{ required: true, validator: checkCourseIds, trigger: "blur"}], 
    //enName: [{ required: true, message: "请输入英文名称", trigger: "blur"}], 
    className: [{ required: true, message: "请选择课程性质", trigger: "blur"}], 
    remarks: [{ required: true, message: "请输入课程描述", trigger: "blur"}]
  },
  api:{
      course:id =>`/basic/course/refInfo/${id}`, //详情
      selectBySpecialty:specialtyId =>`/basic/course/selectBySpecialty/${specialtyId}`, //先修课程
      save:() => `/basic/course/basicedit` //保存
  }
 }
 },
 created(){
    this.selectBySpecialty()
 },
 mounted(){
     this.initData()
     
 },
 methods:{
     initData(){
        const url=this.api.course(this.$route.query.id)
        this.$get(url).then(res =>{
            if(res.status ===1){       
                const obj={
                    courseCode:res.data.courseCode,
                    period:res.data.period,
                    specialName:res.data.specialName,
                    courseName:res.data.courseName,
                    credit:res.data.credit,
                    courseIds:res.data.frontCourseId,
                    enName:res.data.enName || '',
                    className:res.data.className,
                    remarks:res.data.remarks || ''
                }
                this.selectCourse=res.data.frontCourseId ? res.data.frontCourseId.split(',') : []
                this.courseIds=res.data.frontCourseId || ''
                this.form=obj

            }else{
                this.$notify.error({
                    title: "error",
                    duration: 1500,
                    message: res.message
                });
            }
        })
     },
     selectBySpecialty(){
          const url=this.api.selectBySpecialty(localStorage.getItem('specialId'))
          this.$get(url).then(res =>{
              if(res.status ===1){
                   this.courseOptions=res.data;
              }
          })
     },
     selectChange(){
         this.courseIds=this.selectCourse.join(',')
     },
     formSubmit(formName, type){
         const obj={
             id:this.$route.query.id,
             frontCourseIds:this.courseIds,
             remarks:this.form.remarks
         }
         if(type === 'save'){      
             this.$refs[formName].resetFields()
             this.userSave('save',obj)
         }else{ 
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.userSave('next', obj)
                }
            });
         }
       
     },
     userSave(type, formData){
            this.$put(this.api.save(), formData).then( res => {
                if(res.status === 1){
                    this.$notify.success({
                        title: 'success',
                        duration:1500,
                        message: res.message
                        })
                    if(type === 'next'){
                         this.$router.push({
                            path:'/Construction/teachingGoal',
                            query:{
                                id:formData.id,
                                title:this.form.courseName
                            }
                        })
                    }else{
                        this.initData()
                    } 
                }else{
                    this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.message
                    })
                }  
            })
     }
 }
}
</script>

<style lang='scss' scoped>
 @import '../../styles/courseConstruction.scss';
</style>



